import React, { useState } from 'react';
import { 
  Box, 
  Typography, 
  IconButton, 
  Switch, 
  Pagination, 
  PaginationItem,
  Chip,
  FormControl,
  Select,
  MenuItem,
  useMediaQuery,
  useTheme
} from '@mui/material';
import FavoriteIcon from '@mui/icons-material/FavoriteBorder';
import FavoriteFilledIcon from '@mui/icons-material/Favorite';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import PlaceOutlinedIcon from '@mui/icons-material/PlaceOutlined';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import MenuIcon from '@mui/icons-material/Menu';

interface JobListingsProps {
  onMenuOpen?: () => void;
}

interface Job {
  id: number;
  title: string;
  company: string;
  companyLogo: string;
  logoText: string;
  location: string;
  timePosted: string;
  experience: string;
  jobType: string;
  remoteType: string;
  categories: string[];
}

const JobListings: React.FC<JobListingsProps> = ({ onMenuOpen }) => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));
  const [currentPage, setCurrentPage] = useState(2);
  const [favoriteJobs, setFavoriteJobs] = useState<number[]>([]);
  const [sortBy, setSortBy] = useState('Top match');
  
  const toggleFavorite = (jobId: number) => {
    if (favoriteJobs.includes(jobId)) {
      setFavoriteJobs(favoriteJobs.filter(id => id !== jobId));
    } else {
      setFavoriteJobs([...favoriteJobs, jobId]);
    }
  };
  
  const handleChangePage = (event: React.ChangeEvent<unknown>, value: number) => {
    setCurrentPage(value);
  };

  const handleSortChange = (event: any) => {
    setSortBy(event.target.value);
  };
  
  const jobData: Job[] = [
    {
      id: 1,
      title: "Gaming UI designer",
      company: "Rockstar Games",
      companyLogo: "#4285F4",
      logoText: "R",
      location: "ElMansoursa, Egypt",
      timePosted: "10 days ago",
      experience: "0 - 3y of exp",
      jobType: "Full time",
      remoteType: "Remote",
      categories: ["Creative / Design", "IT / Software development", "Gaming"]
    },
    {
      id: 2,
      title: "Senior UX UI Designer",
      company: "Egabi",
      companyLogo: "#0FB5EE",
      logoText: "E",
      location: "Cairo, Egypt",
      timePosted: "1 month ago",
      experience: "0 - 3y of exp",
      jobType: "Full time",
      remoteType: "Hybrid",
      categories: ["Creative / Design", "IT / Software development"]
    },
    {
      id: 3,
      title: "React Frontend developer",
      company: "Magara",
      companyLogo: "#7628EE",
      logoText: "M",
      location: "Cairo, Egypt",
      timePosted: "1 month ago",
      experience: "5 - 7y of exp",
      jobType: "Freelance",
      remoteType: "Remote",
      categories: ["Creative / Design", "IT / Software development"]
    }
  ];

  return (
    <Box sx={{ width: '100%', minHeight: '100%', bgcolor: '#f8f8f8' }}>
      <Box sx={{ 
        bgcolor: '#4CAF50', 
        color: 'white',
        p: { xs: 2, md: 3 },
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        position: 'relative'
      }}>
        {isMobile && (
          <IconButton 
            sx={{ 
              position: 'absolute',
              left: 8,
              top: '50%',
              transform: 'translateY(-50%)',
              bgcolor: 'rgba(255, 255, 255, 0.2)',
              borderRadius: 1,
              p: 0.8,
              '&:hover': {
                bgcolor: 'rgba(255, 255, 255, 0.3)'
              }
            }}
            onClick={onMenuOpen}
          >
            <MenuIcon sx={{ color: 'white', fontSize: '1.2rem' }} />
          </IconButton>
        )}
        
        <Box sx={{ 
          ml: isMobile ? 4 : 0
        }}>
          <Typography 
            variant={isMobile ? "h6" : "h6"} 
            component="h1" 
            sx={{ 
              fontWeight: 500,
              fontSize: { xs: '1.1rem', md: '1.25rem' }
            }}
          >
            UI Designer in Egypt
          </Typography>
          <Typography 
            variant="body2" 
            sx={{ 
              mt: 0.5,
              fontSize: { xs: '0.8rem', md: '0.875rem' }
            }}
          >
            70 job positions
          </Typography>
        </Box>
        
        <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
          <Typography variant="body2" sx={{ display: { xs: 'none', sm: 'block' } }}>
            Set alert
          </Typography>
          <Switch size="small" color="default" />
        </Box>
      </Box>
      
      <Box sx={{ 
        display: 'flex', 
        justifyContent: 'flex-end', 
        alignItems: 'center', 
        px: { xs: 2, md: 3 }, 
        py: 1.5,
        borderBottom: '1px solid rgba(0, 0, 0, 0.08)'
      }}>
        <Typography variant="body2" sx={{ color: 'rgba(0, 0, 0, 0.6)', mr: 1 }}>
          Sorting by:
        </Typography>
        <FormControl 
          variant="standard" 
          size="small" 
          sx={{ 
            minWidth: 120,
            position: 'relative',
            '& .MuiInput-underline:before': { borderBottom: 'none' },
            '& .MuiInput-underline:hover:not(.Mui-disabled):before': { borderBottom: 'none' },
            '& .MuiInput-underline:after': { borderBottom: 'none' }
          }}
        >
          <Select
            value={sortBy}
            onChange={handleSortChange}
            IconComponent={KeyboardArrowDownIcon}
            sx={{ 
              color: '#4CAF50', 
              fontWeight: 500, 
              fontSize: '0.875rem',
              '& .MuiSelect-select': { 
                paddingBottom: 0,
                display: 'flex',
                alignItems: 'center',
                gap: 0.5
              }
            }}
            MenuProps={{
              anchorOrigin: {
                vertical: 'bottom',
                horizontal: 'right',
              },
              transformOrigin: {
                vertical: 'top',
                horizontal: 'right',
              },
              PaperProps: {
                elevation: 3,
                sx: {
                  mt: 1,
                  borderRadius: 1,
                  width: 180,
                  '& .MuiList-root': {
                    p: 0.5
                  }
                }
              }
            }}
          >
            <MenuItem value="Top match" sx={{ borderRadius: 0.5, my: 0.3, p: 1.5 }}>
              <Box sx={{ display: 'flex', alignItems: 'center', width: '100%' }}>
                <Box sx={{ flexGrow: 1 }}>
                  <Typography variant="body2" sx={{ fontWeight: 500 }}>
                    Top match
                  </Typography>
                </Box>
              </Box>
            </MenuItem>
            <MenuItem value="Most recent" sx={{ borderRadius: 0.5, my: 0.3, p: 1.5 }}>
              <Box sx={{ display: 'flex', alignItems: 'center', width: '100%' }}>
                <Box sx={{ flexGrow: 1 }}>
                  <Typography variant="body2" sx={{ fontWeight: 500 }}>
                    Most recent
                  </Typography>
                </Box>
              </Box>
            </MenuItem>
            <MenuItem value="Salary" sx={{ borderRadius: 0.5, my: 0.3, p: 1.5 }}>
              <Box sx={{ display: 'flex', alignItems: 'center', width: '100%' }}>
                <Box sx={{ flexGrow: 1 }}>
                  <Typography variant="body2" sx={{ fontWeight: 500 }}>
                    Salary
                  </Typography>
                </Box>
              </Box>
            </MenuItem>
          </Select>
        </FormControl>
      </Box>
      
      <Box sx={{ 
        bgcolor: 'white',
        borderRadius: 0,
        mx: 0,
        my: 0,
        boxShadow: 'none',
        borderBottom: '1px solid rgba(0, 0, 0, 0.05)',
        px: { xs: 0, md: 0 }
      }}>
        {jobData.map((job) => (
          <Box
            key={job.id}
            sx={{
              p: { xs: 2, md: 2.5 },
              px: { xs: 2, md: 3 },
              borderBottom: '1px solid',
              borderColor: 'rgba(0, 0, 0, 0.08)',
              display: 'flex',
              flexDirection: 'column',
              gap: 1.5,
              transition: 'background-color 0.2s',
              '&:hover': {
                bgcolor: 'rgba(0, 0, 0, 0.01)'
              }
            }}
          >
            <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
              <Box sx={{ display: 'flex', gap: 2 }}>
                <Box 
                  sx={{ 
                    width: { xs: 48, md: 45 }, 
                    height: { xs: 48, md: 45 }, 
                    bgcolor: job.companyLogo,
                    borderRadius: { xs: 1.5, md: 1.2 },
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    color: 'white',
                    fontSize: { xs: 22, md: 20 },
                    fontWeight: 'bold',
                    mt: 0.5
                  }}
                >
                  {job.logoText}
                </Box>
                
                <Box>
                  <Typography 
                    variant="subtitle1" 
                    sx={{ 
                      fontWeight: { xs: 500, md: 600 }, 
                      mb: 0.5,
                      fontSize: { xs: '1rem', md: '1rem' }
                    }}
                  >
                    {job.title}
                  </Typography>
                  
                  <Typography 
                    variant="body2" 
                    sx={{ 
                      color: isMobile ? "#1976d2" : "rgba(0, 0, 0, 0.6)",
                      mb: 1,
                      fontSize: { xs: '0.85rem', md: '0.875rem' },
                      fontWeight: isMobile ? 500 : 400
                    }}
                  >
                    {job.company}
                  </Typography>
                  
                  <Box 
                    sx={{ 
                      display: 'flex', 
                      alignItems: 'center', 
                      gap: { xs: 0.5, md: 1 },
                      mb: 1,
                      flexWrap: 'wrap'
                    }}
                  >
                    <Box sx={{ display: 'flex', alignItems: 'center', gap: 0.5 }}>
                      <PlaceOutlinedIcon sx={{ fontSize: '0.9rem', color: 'rgba(0, 0, 0, 0.5)' }} />
                      <Typography 
                        variant="body2" 
                        sx={{ 
                          color: 'rgba(0, 0, 0, 0.6)', 
                          fontSize: '0.75rem' 
                        }}
                      >
                        {job.location}
                      </Typography>
                    </Box>
                    
                    {isMobile && (
                      <Typography sx={{ color: 'rgba(0, 0, 0, 0.3)', fontSize: '0.75rem' }}>
                        
                      </Typography>
                    )}
                    
                    <Box sx={{ display: 'flex', alignItems: 'center', gap: 0.5 }}>
                      <AccessTimeIcon sx={{ fontSize: '0.9rem', color: 'rgba(0, 0, 0, 0.5)' }} />
                      <Typography 
                        variant="body2" 
                        sx={{ 
                          color: 'rgba(0, 0, 0, 0.6)', 
                          fontSize: '0.75rem' 
                        }}
                      >
                        {job.timePosted}
                      </Typography>
                    </Box>
                  </Box>
                  
                  <Box sx={{ 
                    display: 'flex',
                    alignItems: 'center',
                    flexWrap: 'wrap',
                    gap: { xs: 0.8, md: 1 },
                    mb: 1
                  }}>
                    <Chip 
                      label={job.experience} 
                      variant="outlined" 
                      size="small" 
                      sx={{ 
                        height: 'auto', 
                        fontSize: '0.7rem', 
                        py: 0.3,
                        borderColor: 'rgba(0, 0, 0, 0.12)',
                        color: 'rgba(0, 0, 0, 0.6)',
                        backgroundColor: 'rgba(0, 0, 0, 0.02)'
                      }} 
                    />
                    <Chip 
                      label={job.jobType} 
                      variant="outlined" 
                      size="small" 
                      sx={{ 
                        height: 'auto', 
                        fontSize: '0.7rem', 
                        py: 0.3,
                        borderColor: 'rgba(0, 0, 0, 0.12)',
                        color: 'rgba(0, 0, 0, 0.6)',
                        backgroundColor: 'rgba(0, 0, 0, 0.02)'
                      }} 
                    />
                    <Chip 
                      label={job.remoteType} 
                      variant="outlined" 
                      size="small" 
                      sx={{ 
                        height: 'auto', 
                        fontSize: '0.7rem', 
                        py: 0.3,
                        borderColor: 'rgba(0, 0, 0, 0.12)',
                        color: 'rgba(0, 0, 0, 0.6)',
                        backgroundColor: 'rgba(0, 0, 0, 0.02)'
                      }} 
                    />
                  </Box>
                  
                  <Box sx={{ 
                    display: 'flex', 
                    gap: { xs: 1, md: 2 },
                    flexWrap: 'wrap', 
                    mt: 0.5 
                  }}>
                    {job.categories.map((category, index) => (
                      <Box 
                        key={index} 
                        sx={{ 
                          display: 'flex', 
                          alignItems: 'center',
                          gap: 0.5
                        }}
                      >
                        <Typography 
                          variant="body2" 
                          sx={{ 
                            color: 'rgba(0, 0, 0, 0.5)', 
                            fontSize: '0.75rem' 
                          }}
                        >
                          {category}
                        </Typography>
                        
                        {index < job.categories.length - 1 && (
                          <Typography sx={{ color: 'rgba(0, 0, 0, 0.3)', fontSize: '0.75rem' }}>
                            -
                          </Typography>
                        )}
                      </Box>
                    ))}
                  </Box>
                </Box>
              </Box>
              
              <IconButton 
                onClick={() => toggleFavorite(job.id)} 
                size="small"
                sx={{ height: 'fit-content' }}
              >
                {favoriteJobs.includes(job.id) ? (
                  <FavoriteFilledIcon fontSize="small" color="error" />
                ) : (
                  <FavoriteIcon fontSize="small" sx={{ color: 'rgba(0, 0, 0, 0.3)' }} />
                )}
              </IconButton>
            </Box>
          </Box>
        ))}
      </Box>
      
      <Box sx={{ 
        display: 'flex', 
        justifyContent: 'center', 
        py: 3, 
        bgcolor: 'white' 
      }}>
        <Pagination
          count={3}
          page={currentPage}
          onChange={handleChangePage}
          renderItem={(item) => (
            <PaginationItem
              {...item}
              sx={{
                '&.Mui-selected': {
                  bgcolor: '#4CAF50',
                  color: 'white',
                  '&:hover': {
                    bgcolor: '#43a047',
                  }
                }
              }}
            />
          )}
        />
      </Box>
    </Box>
  );
};

export default JobListings; 